@font-face {
  font-family: 'Lora';
  font-style: italic;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/lora/v20/0QI8MX1D_JOuMw_hLdO6T2wV9KnW-MoFoq92mQ.ttf) format('truetype');
}
@font-face {
  font-family: 'Lora';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/lora/v20/0QI6MX1D_JOuGQbT0gvTJPa787weuxJBkqg.ttf) format('truetype');
}
@font-face {
  font-family: 'Lora';
  font-style: normal;
  font-weight: 700;
  src: url(https://fonts.gstatic.com/s/lora/v20/0QI6MX1D_JOuGQbT0gvTJPa787z5vBJBkqg.ttf) format('truetype');
}
.landin-text {
  height: 2rem;
}
.landin-text main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.landin-text main p {
  display: flex;
  flex-flow: wrap;
  font-size: 0.24rem;
  font-weight: bold;
  font-family: Lora, serif;
  color: #fff;
  white-space: pre;
}
.landin-text main p span {
  animation: landIn 0.8s ease-out both;
}
@media only screen and (max-width: 419px) {
  .landin-text main p {
    font-size: 0.21rem;
  }
}
@keyframes landIn {
  from {
    opacity: 0;
    transform: translateY(-40%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
