@btn-bgcolor: #b1b1b1;
@btn-color: #333;

.wrapper {
  position: relative;
  margin-bottom: .5rem;

  > section {
    display: flex;
    flex-flow: column;
    width: 60vw;
    border-radius: 8px 8px 0 0;
    box-shadow: 0 0 5px #333;
    color: #ddd;
    background-color: #333;
    transition: height .4s ease;
    overflow: hidden;
    
    > h4 {
      box-sizing: border-box;
      height: .4rem;
      padding: .1rem;
      background-color: #444;
      position: relative;
      z-index: 9;
    }

    > main {
      flex: 1;
      padding: .1rem;
    }

    @media only screen and (max-width: 767px) {
      width: 95vw;
    }
  }

  .repeat-btn {
    position: absolute;
    right: 12px;
    top: 6px;
    padding: 6px 8px;
    border: none;
    outline: none;
    border-radius: 4px;
    color: @btn-color;
    box-shadow: 0 0 3px @btn-bgcolor;
    background-color: @btn-bgcolor;
    cursor: pointer;
    z-index: 19;
  }

  .fold-btn {
    position: relative;
    top: -1px;
    width: 100%;
    height: .2rem;
    border: none;
    padding: 4px 0;
    outline: none;
    border-radius: 0 0 8px 8px;
    box-shadow: 0 0 5px #333;
    background: #555 url('../images/fold.svg') no-repeat center / contain;
    transition: background-image .4s;
    cursor: pointer;
    z-index: 9;
  }

  &.fold {
    > section {
      height: .4rem;
    }

    .fold-btn {
      background-image: url('../images/unfold.svg');
    }
  } 
}
