@font-face {
  font-family: 'Raleway';
  font-style: italic;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/raleway/v22/1Pt_g8zYS_SKggPNyCgSQamb1W0lwk4S4WjMDrMfJQ.ttf) format('truetype');
}
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/raleway/v22/1Ptxg8zYS_SKggPN4iEgvnHyvveLxVvaorCIPrQ.ttf) format('truetype');
}
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 700;
  src: url(https://fonts.gstatic.com/s/raleway/v22/1Ptxg8zYS_SKggPN4iEgvnHyvveLxVs9pbCIPrQ.ttf) format('truetype');
}
.reveal-text {
  height: 2rem;
}
.reveal-text main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.reveal-text main p {
  display: flex;
  position: relative;
  font-size: 0.32rem;
  font-family: Raleway, sans-serif;
  color: #6ee1f5;
  letter-spacing: 3px;
  text-transform: uppercase;
  white-space: pre;
}
.reveal-text main p::before,
.reveal-text main p::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  transform: scale(0);
}
.reveal-text main p::before {
  left: 50%;
  animation: slideLeft 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) both;
}
.reveal-text main p::after {
  right: 50%;
  animation: slideRight 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) both;
}
.reveal-text main p span {
  opacity: 0;
  transform: scale(0);
  animation: fadeIn 2.4s both;
}
@media only screen and (max-width: 419px) {
  .reveal-text main p {
    font-size: 0.25rem;
  }
}
@keyframes fadeIn {
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes slideLeft {
  to {
    left: -5%;
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes slideRight {
  to {
    right: -5%;
    opacity: 1;
    transform: scale(1);
  }
}
